.container {
  width: 56px;
  height: 32px;
  font-size: 15px;
  border: 1px solid #afafaf;
  display: inline-block;
  border-radius: 2px;
  margin: 0px;
  vertical-align: bottom;
  transition: all 0.2s;
  text-align: left;
  padding: 4px 12px;
  box-sizing: border-box;
}

.selectValue {
  cursor: pointer;
  padding: 5px;
}

.selectValue:hover {
  background: #afafaf;
}
