.wrapper {
  display: inline-block;
  vertical-align: top;
}

.wrapper .link.flat {
  width: auto;
  height: auto;
  line-height: 0;
  padding: 0;
  background: none;
  border: none;
}

.wrapper .link {
  width: 200px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  font-family: sans-serif;
  text-decoration: none;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;

  display: inline-block;
  padding: 10px;
  border: 1px solid #c7c7c7;
  border-radius: 3px;
  background: #e9f0f7;
  color: #000;
  font-size: 20px;

  cursor: pointer;
}

.wrapper .link .content {
  position: relative;
  z-index: 2;
}

.wrapper .link:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #ff003b;
  transition: all .35s;
  border-radius: 10px;
}

.wrapper .link.clicked {
  color: #fff;
}

.wrapper .link.clicked:after {
  width: 100%;
}


