.modalContainer {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  text-align: center;
  z-index: 999;
}

.modalBackground {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: #000000;
  opacity: 0.5;
  filter: alpha(opacity=50);

  transition: all 0.2s ease-in-out;
  z-index: 998;
}

.modalInner {
  position: relative;
  display: inline-block;
  overflow: auto;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);

  text-align: left;

  padding: 12px;
  background-color: #fafafa;
  border: 7px solid rgba(0, 0, 0, 0.4);
  border-radius: 2px;
  background-clip: padding-box;

  transition: all 0.2s ease-in-out;
}
