.button {
  display: inline-block;
  position: relative;
  vertical-align: top;
  padding: 4px;
  border: 1px solid #ccc;
  cursor: pointer;
  border-radius: 4px;
  background: #f5faff;
  transition: background 0.3s;
  user-select: none;
  touch-action: none;
  overflow: hidden;
  box-sizing: border-box;

  margin: 2px;
}

.clicked {
  background: #ff2222;
}

.content {
  position: relative;
  pointer-events: none;
  user-select: none;
  touch-action: none;
  white-space: nowrap;
}

.icon {
  vertical-align: middle;
  max-height: 30px;
  max-width: 60px;
  height: auto;
  width: auto;
  pointer-events: all;
}

.text {
  display: inline-block;
  vertical-align: middle;
  padding-left: 4px;
}

.fillBar {
  position: absolute;
  /* height: calc(100% - 8px); */
  height: 3px;
  top: calc(100% - 3px);
  background: #ffa300;

  pointer-events: none;
  user-select: none;
  touch-action: none;
}

.mainText {
  display: block;
  position: relative;
  font-size: 14px;
}

.subText {
  display: block;
  position: relative;
  font-size: 10px;
}